li {
    padding: 15px;
    background-color: orange;
    width: 350px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    
}

.input__checkbox {
    color: #ff1;
    :hover {
        width: 115px;
    }
}

.item {
    max-width: 300px;
    flex-wrap: wrap;
    &:hover {
        background: rgb(21, 255, 0);
        transition: 0.5s;
    }
    &:checked {
        background: #888;
        color: #fff;
        text-decoration: line-through;
    }
    
}

.item__dashed {
    text-decoration: underline;
}

.button {
    background-color: red;
    border: none;
    outline: none;
    border-radius: 3px;
    height: 30px;

    &.warning {
        background-color: #ff1;
    }
}

.btn__wrapper {
    display: flex;
    gap: 5px;
}

