body {
    background-color: #affa00;
    display: flex;
    justify-content: center;
    padding-top: 30px;
}

.container {
    text-align: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    padding: 0 20px;
}

.form {
    width: 400px;
}   

.input {
    outline: none;
    border: none;
    margin: 0;
    border: none;
    border-radius: 0;
    padding: 10px;
    font-size: 16px;
    border-radius: 20px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: orange;
    color: black;
}

.btn {
    padding: 10px;
    background: #ffb700;
    font-size: 16px;
    cursor: pointer;
    transition: 0.5s;
    border-radius: 40%;
    outline: none;
    border: none;
    margin-left: 20px;
}

.btn:hover {
    background-color: #affa00;
}
